import Vue from "vue";
import Vuex from "vuex";
import mutations from "./mutations"
import actions from "./actions"

//
Vue.use(Vuex);

//store
const store = new Vuex.Store({
	//
	state:{
		//
			topics:[
  			{name:"all",desc:""},
  			{name:"good",desc:""},
  			{name:"share",desc:""},
  			{name:"ask",desc:""},
  			{name:"job",desc:""},
  			{name:"dev",desc:""},
  		],
  		unLoginedMoreMenus:{ //
  			login:""
  		},
  		loginedMoreMenus:{  //
  			create:"",
  			center:"",
  			msg:"",
  			logout:""
  		},
  		isLogined:false,  //
  		loginname:'',     //
  		accesstoken:'',	  //accesstoken
  		author_id:"",     //id
  		isShowMoreMenus:false  //actionsheet
  },
  	
	
	//
	getters:{
		moreMenus:function(state){
	  		if(state.isLogined){//true
	  			return state.loginedMoreMenus;//
	  		}
	  		return state.unLoginedMoreMenus;//
	  	},
	  	getTopicDescByTab: function(state) {
	//
	return function(tab) {
		for(var index = 0; index < state.topics.length; index++) {
			if(state.topics[index].name == tab) {
				return state.topics[index].desc;
			}
		}
	};

}
	},
	mutations,
	actions
	
});

export default store;






























